ent {\n  @include medium_kor;\n  font-size: 14px;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n}\n\n.review--posting--commentLength {\n  @include small_kor;\n  color: $darkgrey_medium;\n  &:hover {\n    cursor: pointer;\n  }\n}\n\n.review--posting--createdAt {\n  @include small_kor;\n  color: $darkgrey_light;\n  font-size: 12px;\n}\n\n.review--posting--open_modal {\n  position: absolute;\n  left: 245px;\n  top: 405px;\n  display: flex;\n  align-items: center;\n  span {\n    @include small_kor;\n    color: $darkgrey_light;\n  }\n  img {\n    width: 8px;\n    padding-top: 1px;\n  }\n  &:hover {\n    cursor: pointer;\n  }\n}\n\n.position-right {\n  left: inherit;\n  right: 10px;\n}\n\n.footer--empty {\n  margin-bottom: 550px;\n}\n","@import '@css/fonts.scss';\n\n$basic_width: 700px;\n\n@mixin basic--wrappers {\n  width: $basic_width;\n  margin: 0 auto;\n}\n","// family\n$kor_font_regular: 'Noto Sans KR', sans-serif;\n$eng_font_regular: 'Roboto', sans-serif;\n\n// color\n$darkgrey_darker: #252323;\n$darkgrey_default: rgb(80, 80, 80);\n$darkgrey_medium: rgb(140, 140, 140);\n$darkgrey_light: rgb(184, 184, 184);\n$lightblue: #73b2ed;\n$pinkred: #ec7063;\n$red_for_submit: #9f062ba3;\n\n//size\n$main_title_22: 22px;\n$sub_title_18: 18px;\n$default_medium_15: 15px;\n$default_small_13: 13px;\n\n// font-set\n@mixin medium_kor {\n  font-family: $kor_font_regular;\n  font-size: $default_medium_15;\n  color: $darkgrey_default;\n}\n\n@mixin small_kor {\n  font-family: $kor_font_regular;\n  font-size: $default_small_13;\n  color: $darkgrey_default;\n}\n\n@mixin medium_eng {\n  font-family: $eng_font_regular;\n  font-size: $default_medium_15;\n  color: $darkgrey_default;\n}\n\n@mixin small_eng {\n  font-family: $eng_font_regular;\n  font-size: $default_small_13;\n  color: $darkgrey_default;\n}\n"],"sourceRoot":""}]);
