x;\n$right_devision_width: 410px;\n$comment_input_width: 330px;\n\n.review--detail--background {\n  position: fixed;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  background-color: #9e9e9eb8;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  z-index: 2;\n}\n\n.review--detail--xmark {\n  position: absolute;\n  right: 50px;\n  top: 50px;\n  width: 25px;\n  &:hover {\n    cursor: pointer;\n  }\n}\n\n.review--detail-devision {\n  display: flex;\n  width: $total_width;\n  height: $total_height;\n  background-color: white;\n  z-index: 10;\n}\n\n.review--detail--left_division {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  position: relative;\n  width: $left_devision_width;\n  height: $total_height;\n}\n\n.review--detail--image--background {\n  position: absolute;\n  top: 0px;\n  left: 0px;\n  height: $total_height;\n  width: $left_devision_width;\n  background-color: #b8b8b84d;\n  z-index: -1;\n}\n\n.review--detail--modal_image {\n  img {\n    width: $left_devision_width;\n    object-fit: cover;\n  }\n}\n\n.review--detail--right_division {\n  width: $right_devision_width;\n  display: flex;\n  flex-direction: column;\n}\n\n.review--detail--header {\n  display: flex;\n  justify-content: space-between;\n  align-items: flex-end;\n  margin: 20px $right_devision_margin;\n  border-bottom: 1px solid $darkgrey_light;\n  span {\n    display: block;\n  }\n}\n\n.review--detail--title {\n  @include medium_kor;\n  font-size: 16px;\n  max-width: 250px;\n  display: flex;\n  align-items: end;\n  padding-bottom: 7px;\n}\n\n.review--detail--members {\n  display: flex;\n  img {\n    width: 30px;\n    margin-left: 6px;\n  }\n}\n\n.review--detail--detail_comments {\n  width: $right_devision_width;\n  height: 290px;\n  margin-top: 10px;\n  padding: 0px $right_devision_margin;\n  overflow-y: scroll;\n  &::-webkit-scrollbar {\n    display: none;\n  }\n}\n\n.review--detail--comment_wrapper {\n  margin-bottom: 30px;\n}\n\n.review--detail--writter {\n  @include medium_eng;\n  font-weight: bold;\n  margin-right: 7px;\n}\n\n.review--detail--full_comment {\n  @include medium_kor;\n}\n\n.review--detail--post_comment--wrapper {\n  display: flex;\n  justify-content: end;\n  align-items: flex-end;\n  width: $right_devision_width;\n  height: 60px;\n}\n\n.review--detail--form {\n  display: flex;\n  justify-content: space-between;\n  align-content: center;\n  width: $right_devision_width - ($right_devision_margin * 2);\n  margin: 0 15px 15px 15px;\n}\n\n.review--detail--input {\n  @include medium_kor;\n  height: 24px;\n  width: $comment_input_width;\n  border: none;\n  &:focus {\n    outline: none;\n  }\n}\n\n.review--detail--button--forFlex {\n  display: flex;\n  justify-content: end;\n}\n\n.review--detail--button {\n  @include medium_kor;\n  color: $lightblue;\n  background: none;\n  padding: 0;\n  border: none;\n  border-radius: 10px;\n  &:hover {\n    cursor: pointer;\n  }\n}\n","// family\n$kor_font_regular: 'Noto Sans KR', sans-serif;\n$eng_font_regular: 'Roboto', sans-serif;\n\n// color\n$darkgrey_darker: #252323;\n$darkgrey_default: rgb(80, 80, 80);\n$darkgrey_medium: rgb(140, 140, 140);\n$darkgrey_light: rgb(184, 184, 184);\n$lightblue: #73b2ed;\n$pinkred: #ec7063;\n$red_for_submit: #9f062ba3;\n\n//size\n$main_title_22: 22px;\n$sub_title_18: 18px;\n$default_medium_15: 15px;\n$default_small_13: 13px;\n\n// font-set\n@mixin medium_kor {\n  font-family: $kor_font_regular;\n  font-size: $default_medium_15;\n  color: $darkgrey_default;\n}\n\n@mixin small_kor {\n  font-family: $kor_font_regular;\n  font-size: $default_small_13;\n  color: $darkgrey_default;\n}\n\n@mixin medium_eng {\n  font-family: $eng_font_regular;\n  font-size: $default_medium_15;\n  color: $darkgrey_default;\n}\n\n@mixin small_eng {\n  font-family: $eng_font_regular;\n  font-size: $default_small_13;\n  color: $darkgrey_default;\n}\n"],"sourceRoot":""}]);
